.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 160px;
  height: 55px;
  background-color: blueviolet;
  color: white;
  font-size: 1.5em;
  cursor: pointer;
  border: none;
}

.button > * {
  margin: 5px;
}

.swapViewIcon {
  position: relative;
  top: 4px;
  font-size: 1.2em;
}
